.last_container {
  padding: 20px;
  text-align: center;
}

.box_area {
  display: flex;
  align-items: center;
  margin: 0 0 20px;
}

.box_area .box {
  flex: 1 1 0;
  height: 500px;
  margin: 10px;
  border: 1px solid #000;
}

.btn_area button {
  margin: 0 10px;
  padding: 5px 10px;
  font-size: 16px;
}

.box.win {
  color: green;
  border: 10px solid green;
}

.box.lose {
  color: red;
  border: 10px solid red;
}

.box.tie {
  color: #000;
  border: 10px solid #000;
}
